import React, { Component } from 'react';
import { Switch, Route,Link } from 'react-router-dom';
import logo from './logo.svg';
import './App.css';
import Users from './Users';
import View from './View';
import { browserHistory } from 'react-router-dom';
import {withRouter} from "react-router-dom";
const fetch=require('node-fetch');
const Token=`4d7f647a7d6e251ded575839644210d96b729e4d`;
const BASE_URL=`https://api.github.com/search/users?acced_token=${Token}`;
class App extends Component {
  constructor(props){
    super(props);
    this.state={
      data:null,
      num:0,
      ind:0,
      name:null,
    }
    this.onInputChange=this.onInputChange.bind(this);
  }
  search(query){
    fetch(`${BASE_URL}&q=${query}`)
    .then((res)=>res.json())
      .then((res)=>{
        this.setState({
          data:res,
        });
        if(this.state.data.items){
          this.setState({
            num:res.items.length,
            name:query,
          });
          this.props.history.push(`/search/${this.state.name}/1`);
        }
    })
    .catch(err=>console.error(err));
  }
  
  onInputChange(e){
    const {value}=e.target;
    if(!value){
      return;
    }
    this.search(value);
  }

  Arr() {
    var arr=[];
    for(let i=1;i<this.state.num/3;i++)
      arr.push(i);
    const listItems = arr.map((number,ind) =>
      <li  key={ind}><Link to={`/search/${this.state.name}/${number}`}>{number}</Link></li>
    );
    return listItems;
   
   }
  render() {
    const {data}=this.state;
    const {num}=this.state;
    return (
      <div className="App">
        <input type="text"  onChange={this.onInputChange}/>
        <Switch location={this.props.location}>
          <Route exact path="/" render={props => (<h1>Write something!</h1>)} />
          <Route  path={`/search/${this.state.name}/:number`} render={props => (<Users {...props} state={this.state}/>)} />
        </Switch>
        <Route path="/about/:login" render={props => (<View {...props} state={this.state}/>)} />
        <ul className='hr'>
          {
            this.Arr()
          }

        </ul>
      </div>
    );
  }
}

export default withRouter(App);



