import React, { Component } from 'react';
import Link from 'react-router-dom/Link';
import {withRouter} from "react-router-dom";
import './View.css';
class View extends Component {
    constructor(props){
        super(props);
        this.back=this.back.bind(this);
    }
     back(e) {
        e.stopPropagation();
        this.props.history.goBack();
      };
      do(){
        var a=this.props.state.data.items;
        for(let i=0;i<a.length;i++)
            if(a[i].login==this.props.match.params.login){
                return(
                    <div >
                        <img 
                        height="160"
                        src={a[i].avatar_url}
                        alt={`${a[i].login} avatar`}/>
                        <h3><a href={`${a[i].html_url}`} target="_blank">{a[i].login}</a></h3>
                        <h5>Score:{a[i].score}</h5>
                    </div>
                )

            }
      }
      render(){
      return (
        <div className="back" onClick={this.back}>
          <div
            className="front"
            style={{
              position: "absolute",
              top: 25,
              left: "30%",
              right: "30%",
              padding: 15,

            }}
          > {this.do()}
            <button type="button" onClick={this.back}>
              Close
            </button>
          </div>
        </div>
      );
    }
    
}

export default View;


