import React, { Component } from 'react';
import Link from 'react-router-dom/Link';

class Users extends Component {
    List(data) {
        let p=parseInt(this.props.match.params.number, 10);
        p--;
        return data.slice(p*3,p*3+3);
       
       }
      render(){
        const {data}=this.props.state;
      return (
        <div>
        <ul>
          {data && data.items&& this.List(data.items).map(({id,login,avatar_url})=>(
            <li className='list-item' key={id}>
            <Link to={`/about/${login}`}>
            <img
              height='160'
              className='img'
              src={avatar_url}
              alt={`${login} avatar`}/>
              <p>{login}</p>
              </Link>
              </li>
          ))}

        </ul>
          
        </div>
      )
    }
}

export default Users;


